import { Form, Field } from 'react-final-form';
import classes from './ProfileDataForm.module.css';
import { useState } from 'react';


const ProfileDataForm = (props) => {
    const [isSaving, setIsSaving] = useState(false)
    const onSubmit = (e) => {
        setIsSaving(true);
        e.LookingForAJobDescription = 'null';
        props.saveProfile(e).then(() => setIsSaving(false));
        
    }
    const validate = (e) => {
        const errors = {};
        if (false) {
            errors.email = 'Enter a valid email';
        }
        return errors;
    }
    
    return (
        <div className={classes.profileForm}>
            <Form
                onSubmit={onSubmit}
                validate={validate}
                render={({ handleSubmit }) => (
                    <form onSubmit={handleSubmit}>
                        <Field
                            initialValue={props.initialValue.fullName}
                            name="fullName"
                            render={({ input, meta }) => (
                                <div className={classes.inputContainer}>
                                    <label className={classes.label}>Full Name</label>
                                    <input className={classes.input} {...input} placeholder='Full Name' />
                                    {meta.touched && meta.error && <span>{meta.error}</span>}
                                </div>
                            )}
                        />
                        <Field
                            initialValue={props.initialValue.aboutMe}
                            name="AboutMe"
                            render={({ input, meta }) => (
                                <div className={classes.inputContainer}>
                                    <label className={classes.label}>About me</label>
                                    <textarea className={classes.input}  {...input} placeholder='About me' />
                                    {meta.touched && meta.error && <span >{meta.error}</span>}
                                </div>
                            )}
                        />
                     {/*    <Field
                            name="lookingForAJob"
                            type='checkbox'
                            render={({ input, meta }) => (
                                <div>
                                    <label className={classes.label}>Looking for a job</label>
                                    <input type='checkbox' className={classes.checkbox}  {...input} />
                                    {meta.touched && meta.error && <span >{meta.error}</span>}
                                </div>
                            )}
                        /> */}
                        <div>Contacts:{Object.keys(props.profile.contacts)
                            .filter(key => key !== 'mainLink' && key !== 'vk' && key !== 'website')
                            .map(key => {
                           return <Field
                           initialValue={props.initialValue.contacts[key]}
                           key={key}
                           name={`contacts.${key}`}
                           render={({ input, meta }) => (
                            <div className={classes.inputContainer}>
                                   <label className={classes.label}>{key}</label>
                                   <textarea className={classes.input}  {...input} placeholder={key} />
                                   {meta.touched && meta.error && <span >{meta.error}</span>}
                               </div>
                           )}
                       />
                       })}
                        </div>
                        <div>
                            <button disabled={isSaving} className={isSaving ? classes.disabledButton : classes.saveButton} type="submit">Save</button>
                        </div>
                    </form>
                )} />
        </div>
    )
}

export default ProfileDataForm;